<template>
    <div class="quiz-wrap">
        <div class="container">
            <div class="quiz" id="quiz">
                <div class="progress-contain">
                    <div class="progress-bar" :style="'width: ' + progressBar" id="progress-bar"></div>
                </div>
                <div class="quiz-indicator" id="indicator">{{Number(activeIndex)+1}}/{{DATA.length}}</div>
                <div class="quiz-questions" id="questions" data-current-step="0">
                    <div class="quiz-questions__block">
                        <h2 class="quiz-questions__title">{{DATA[activeIndex].title}}</h2>
                        <ul class="quiz-questions__list">
                            <li :class="DATA[activeIndex].class" v-for="item in DATA[activeIndex].answers" :key="item.id" v-on="DATA[activeIndex].type === 'input' ? {} : !DATA[activeIndex].validateButtonNext ? { click: nextQuiz } : {click: checkItem}">
                                <div class="quiz-questions__text-wrap">
                                    <b class="quiz-questions__btn-title">{{item.title}}</b>
                                    <span class="quiz-questions__btn-text">{{item.text}}</span>
                                </div>
                                <img :src="require(`../assets/img/${item.value}`)" alt="" class="img" v-if="DATA[activeIndex].type === 'list'">
                                <div :class="item.class ? 'quiz-questions__input-wrap ' + item.class : 'quiz-questions__input-wrap'" v-if="DATA[activeIndex].type === 'input'">
                                    <input type="number" min="1" :placeholder="item.placeholder || ''" class="input-number item--next" @input="validateInput">
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="quiz-controls">
                    <a class="quiz-controls__prev" id="prev" @click="prevQuiz">
                        <img class="prev" src="../assets/img/prev.647714c9.svg" alt="">
                    </a>
                    <a :class="showNextButton ? 'next quiz-controls__next' : 'next quiz-controls__next btn--disabled'" id="next" @click="nextQuiz">далее</a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { data } from "../data"

    export default {
        name: "Quiz",
        data: function () {
            return {
                activeIndex: 0,
                DATA: data,
                showNextButton: false,
                // progressBar: '0%'
            }
        },
        computed: {
            progressBar() {
                return (this.activeIndex * 100 / this.DATA.length) + '%';
            }
        },
        methods: {
            calculateProgressBar () {
                if (this.activeIndex === 0) {
                    this.progressBar = '0%'
                } else if (this.activeIndex === this.DATA.length) {
                    this.progressBar = '100%'
                } else {
                    this.progressBar = (this.activeIndex * 100 / this.DATA.length) + '%'
                }
            },
            nextQuiz: function () {
                if (this.activeIndex < this.DATA.length - 1) {
                    this.activeIndex++;
                    localStorage.setItem('quizIndex', this.activeIndex);
                } else {
                    this.$router.push({ name: 'Form' })
                }
                this.showNextButton = false
                this.resetGallery()
                this.resetInput()
                this.calculateProgressBar()
            },
            prevQuiz: function () {
                if (this.activeIndex > 0) {
                    this.activeIndex--;
                    localStorage.setItem('quizIndex', this.activeIndex);
                } else {
                    this.$router.push({ name: 'Home' })
                }
                this.showNextButton = false
                this.resetGallery()
                this.resetInput()
                this.calculateProgressBar()
            },
            resetGallery: function () {
                const btnBorderItems = document.querySelectorAll('.btn-border')
                for (let i = 0; i < btnBorderItems.length; i++) {
                    btnBorderItems[i].classList.remove('btn-border')
                }
            },
            resetInput: function() {
                const input = document.querySelector('.input-number')
                if (input) input.value = ''
            },
            checkItem: function (event) {
                let isMultiply = event.currentTarget.classList.contains('multiply');
                if (isMultiply) {
                    event.currentTarget.classList.toggle('btn-border');
                } else {
                    event.currentTarget.parentElement
                    .querySelectorAll('.quiz-questions__item')
                    .forEach((item) => {
                        item.classList.remove('btn-border');
                    });
                    event.currentTarget.classList.add('btn-border');
                }
                const btnBorderItem = document.getElementsByClassName('btn-border')[0]
                if (btnBorderItem) {
                    this.showNextButton = true
                } else {
                    this.showNextButton = false
                }
            },
            validateInput: function (event) {
                if (event.currentTarget.validity.valid && event.currentTarget.value > 0) {
                    this.showNextButton = true
                } else {
                    this.showNextButton = false
                }
            }
        },
        mounted() {
            this.activeIndex = localStorage.getItem('quizIndex') ? localStorage.getItem('quizIndex') : 0;
            if (!localStorage.getItem('quizIndex')) {
                localStorage.setItem('quizIndex', this.activeIndex);
            }
        }
    };

</script>

<style scoped lang="scss">
    @import "../assets/styles/quiz.scss";
</style>