                            данных, Политике, Положению и иным локальным актам, включающий контроль за принимаемыми мерами
                            по
                            обеспечению безопасности персональных данных и их уровня защищенности при обработке в
                            информационной
                            системе Оператора.
                            6. Права субъектов персональных данных</p>
                        <h2>6.1. Субъект персональных данных имеет право:

                        </h2>
                        <p>
                            на получение персональных данных, относящихся к данному субъекту, и информации, касающейся их
                            обработки;
                            на уточнение, блокирование или уничтожение его персональных данных в случае, если они являются
                            неполными, устаревшими, неточными, незаконно полученными или не являются необходимыми для
                            заявленной
                            цели обработки;
                            на отзыв данного им согласия на обработку персональных данных;
                            на защиту своих прав и законных интересов, в том числе на возмещение убытков и компенсацию
                            морального вреда в судебном порядке;
                            на обжалование действий или бездействия Оператора в уполномоченный орган по защите прав
                            субъектов
                            персональных данных или в судебном порядке.
                        </p>
                        <p>6.2. Для реализации своих прав и законных интересов субъекты персональных данных имеют право
                            обратиться к Оператору либо направить запрос лично или с помощью представителя. Запрос должен
                            содержать сведения, указанные в ч. 3 ст. 14 ФЗ «О персональных данных».
                        </p>
                        <div class="d-flex justify-content-end">
                            <button type="button" class="modal-close" @click="isModalOpen = false">
                                <span aria-hidden="true">Close</span>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    import Cleave from 'vue-cleave-component';

    export default {
        name: "FormTwo",
        data: function () {
            return {
                isModalOpen: false,
                price: this.$route.params.price + ' ₽',
                cardMonthYear: '',
                cardNumber: '',
                isCardMonthYearValid: false,
                isCardNumberValid: false,
                isCardCVCValid: false,
                cardCVC: '',
                cardName: '',
                ErrorMSG: '',
                options: {
                    creditCard: true,
                    delimiter: ' ',
                },
                dateOptions: {
                    date: true,
                    datePattern: ['m', 'y']
                },
                cardMonthData: {
                    errorText: ''
                },
                cardNumberData: {
                    errorText: ''
                },
                cardCVCData: {
                    errorText: ''
                },
                cardNameData: {
                    errorText: ''
                }
            }
        },
        components: {
            Cleave
        },
        computed: {
            isCardNameValid() {
                return this.cardName.length > 3;
            }
        },
        methods: {
            sendForm: function () {
                const data = { cardNumber: this.cardNumber, cardMonthYear: this.cardMonthYear, cardCVC: this.cardCVC, cardName: this.cardName }
                axios
                    .post("/payment/", data)
                    .then(response => {
                        if (response.status >= 200 && response.status < 300) {
                            this.redirect();
                        }
                        if (response.status >= 400) {
                            response.paymentMethod.forEach((item) => {
                                this.ErrorMSG += item;
                            });
                            this.cardMonthData.errorText = response.paymentMethod.expiryDate;
                            this.cardNumberData.errorText = response.paymentMethod.pan;
                            this.cardCVCData.errorText = response.paymentMethod.cvv2;
                            this.cardNameData.errorText = response.paymentMethod.holderName;
                        }
                    })
                    .catch(e => {
                        console.log(e)
                    })
            },
            redirect: function () {
                this.$router.push({ name: 'Success' })
            },
            toggleButton: function (event) {
                const oldActive = document.getElementsByClassName('active')[0]
                if (oldActive) oldActive.classList.remove('active')
                event.currentTarget.classList.toggle('active')
            },
            cardNumberValidate: function (event) {
                if (event.currentTarget.validity.valid && event.currentTarget.value !== '') {
                    this.isCardNumberValid = true
                } else {
                    this.isCardNumberValid = false
                }
            },
            cardCVCValidate: function (event) {
                if (event.currentTarget.validity.valid && event.currentTarget.value !== '') {
                    this.isCardCVCValid = true
                } else {
                    this.isCardCVCValid = false
                }
            },
            cardMonthYearValidate: function (event) {
                if (event.currentTarget.validity.valid && event.currentTarget.value !== '') {
                    this.isCardMonthYearValid = true
                } else {
                    this.isCardMonthYearValid = false
                }
            }
        }
    };
</script>

