<script>
    import Swiper, { Navigation, Pagination } from 'swiper'
    import 'swiper/swiper-bundle.css'

    export default {
        name: "StepThree",
        data: function () {
            return {
                isModalOpen: false,
                isChecked: false,
                discount: Math.round(-(890-this.$route.params.price)*100/890) + '%',
                price: this.$route.params.price,
                subscripModalOpen: false
            }
        },
        methods: {
            toggleChecked: function () {
                this.isChecked = !this.isChecked
            },
            scrollButtonClick: function (event) {
                event.preventDefault()
                const offer = document.getElementById('offer1')
                window.scrollTo({ top: offer.offsetTop - 84, behavior: 'smooth' });
            },
            subscripModalOn: function() {
                this.subscripModalOpen = true;
                setTimeout(() => {
                    this.$refs.subscribeCheckbox.checked = 'checked';
                }, 600);
            }
        },
        mounted() {
            const secondSliderContainer = document.querySelector('.secondslider-container')
            if (secondSliderContainer) initSllider()
            function initSllider() {
                const secondPageSlider = new Swiper('.secondpage-slider', {
                    modules: [Navigation, Pagination],
                    loop: true,
                    slidesPerView: 1,
                    spaceBetween: 20,
                    pagination: {
                        el: '.second-pagination',
                        clickable: true
                    },
                    navigation: {
                        nextEl: '.second-next',
                        prevEl: '.second-prev'
                    },
                    breakpoints: {
                        992: {
                            slidesPerView: 2,
                            spaceBetween: 20
                        },
                        1440: {
                            slidesPerView: 3,
                            spaceBetween: 20
                        }
                    }
                });
            }

            let fixedBtnWrapper = document.querySelector('.secondpage-getbutton-wrapper');
            window.addEventListener('scroll', () => {
                if (window.scrollY >= 1000) {
                    fixedBtnWrapper.classList.add('visible');
                } else {
                    fixedBtnWrapper.classList.remove('visible');
                }
            });

            // eslint-disable-next-line no-tabs
            function getSeconds(minutes) {
                return minutes * 60;
            }

            let timerTime = getSeconds(10);

            function timer() {
                let hours = '00';
                let minutes = Math.floor(timerTime / 60);
                minutes = minutes < 10 ? '0' + minutes : minutes;
                let seconds = timerTime % 60;
                seconds = seconds < 10 ? '0' + seconds : seconds;
                let timeArr = [hours, minutes, seconds];

                let timerBoxes = document.querySelectorAll('.timer__box');

                timerBoxes.forEach((box) => {
                    let items = box.querySelectorAll('.timer__block');
                    items.forEach((item, i) => {
                        item.textContent = timeArr[i];
                    });
                });

                timerTime--;
                if (timerTime < 0) {
                    clearInterval(timerInterval);
                }
            }

            let timerInterval = setInterval(timer, 1000);
        }
    };
</script>

